.banner__group {
    overflow: hidden;
    display: grid;
    @include standard-grid();
    grid-column-gap: 1.5rem;
    grid-template-rows: auto;

    @include media('<=mobile') {
        grid-template-columns: repeat(6, 1fr);
        grid-template-areas:
            'image image image image image image'
            'sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap';
    }

    @include media('>mobile') {
        grid-template-columns: repeat(12, 1fr);
        grid-template-areas:
            'image image image image image image image image image image image image'
            'sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap';
    }

    @include media('>desktop') {
        grid-template-columns: repeat(18, 1fr);
        grid-template-areas:
            'image image image image image image image image image image image image image image image image image image'
            '. . . sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap sf-wrap . . .';
    }
}

.banner__group__image {
    grid-area: image;
    height: 100%;
    max-height: $banner-image-max-height;

    .hero-image {
        display: block;
        width: 100%;
        overflow: hidden;

        img {
            width: 100%;
        }
    }

    @include media('<=tablet') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('>large') {
        max-height: none;
    }
}

.banner__group__overlay {
    background: linear-gradient(to top, $color-gray9, transparent 100%);
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;

    @include media('<tablet') {
        height: 100%;
    }
}

.banner__group__overlay__heading {
    @include standard-margins();
    @include standard-grid();
    padding-top: $measure--small4;
    padding-bottom: $measure--small4;
    display: flex;
    align-items: flex-end;
    height: 100%;

    h1 {
        color: $color-purewhite;
        width: 54ch;
        @include text-shadow();
    }

    @include media('<large') {
        h1 {
            width: 100%;
        }
    }

    @include media('<tablet') {
        bottom: $measure--small1;
        h1 {
            width: 100%;
        }
    }

    @include media('<mobile') {
        margin: 0 $measure--small4;
        bottom: $measure--small1;
    }
}

.banner__group__overlay__heading--title-standfirst {
    grid-area: sf-wrap;
    width: 100%;

    @include standard-margins();
    @include standard-grid();

    display: grid;

    @include media('<=mobile') {
        grid-template-columns: repeat(6, 1fr);
        grid-template-areas: 'standfirst standfirst standfirst standfirst standfirst standfirst';
        margin: -$border-width-default 0 0 0;
        padding: 0;
    }

    @include media('>mobile') {
        grid-template-columns: repeat(12, 1fr);
        grid-template-areas: ' standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst ';
        margin: -$border-width-default auto 0 auto;
        padding: 0;
    }

    @include media('>desktop') {
        grid-template-columns: repeat(18, 1fr);
        grid-template-areas: 'standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst standfirst';
        margin-top: -$measure--large4;
    }

    h1 {
        color: $color-purewhite;
        width: auto;
        @include text-shadow();
        @include h1style();
    }
}

.banner__group__overlay__title-standfirst-wrapper {
    grid-template-rows: auto;
    grid-area: standfirst;
    background: $color-blue90;
    color: $color-purewhite;
    opacity: 0.9;
    padding: $measure--small4;
    z-index: 1;
}
