.key-information--lifelong-learning {
    .lifelong-learning__start-date__cta {
        max-width: unset;
        .cta-block__cta {
            @include media('<=between') {
                display: flex;
                flex-direction: column;
                margin-top: $measure--small4;
            }

            a,
            button {
                @include cta-basic-light-bg($color-pink80, $color-purewhite);

                @include media('<=between') {
                    justify-content: center;
                }
            }
        }
    }

    ul {
        list-style-type: none;
        margin-left: 0;
        margin-top: 0;
    }

    > ul {
        > li {
            display: grid;
            padding-bottom: $measure--basic;

            @include media('>=tablet') {
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray6;

                &:last-of-type {
                    border-bottom: none;
                }
            }

            @include media('<=mobile') {
                background-color: $color-purewhite;
                border: $border-width-default $border-style-solid $color-gray6;
                padding: $measure--basic;

                grid-column-gap: $measure--basic;
                grid-template-columns: repeat(6, 1fr);
                grid-template-rows: repeat(4, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading'
                    'date date date date date date'
                    'location location location location location location'
                    'button button button button button button';
            }

            @include media('>mobile', '<=tablet') {
                grid-column-gap: $measure--basic;
                grid-template-columns: repeat(12, 1fr);
                grid-template-rows: repeat(4, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date date date date date date date date'
                    'location location location location location location location location location location location location'
                    'button button button button button button button button button button button button';
            }

            @include media('>tablet', '<=between') {
                grid-column-gap: $measure--basic;
                grid-template-columns: repeat(12, 1fr);
                grid-template-rows: repeat(2, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date location location location location location button button';
            }

            @include media('>between') {
                grid-column-gap: $measure--small2;
                grid-template-columns: repeat(18, 1fr);
                grid-template-rows: repeat(2, auto);
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date date date location location location location location location location location button button button';
            }

            @include media('>desktop') {
                grid-template-areas:
                    'heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading heading'
                    'date date date date date date location location location location location location location button button button button button';
            }

            > h3 {
                grid-area: heading;

                @include media('<=mobile') {
                    margin-top: 0;
                }
            }

            > ul {
                @include media('<tablet') {
                    margin-left: $measure--small4;
                    margin-right: $measure--small4;
                }

                &:nth-of-type(1) {
                    grid-area: date;
                }

                &:nth-of-type(2) {
                    grid-area: location;
                }

                &:nth-of-type(3) {
                    grid-area: button;
                }
            }
        }
    }
}
