.banner__content {
    @include standard-grid();
    grid-template-areas:
        'l l l l l l l l l l l l l l l l l l'
        'h h h h h h h h h h h h h h h h h h'
        'c c c c c c c c c c c c c c c c c c';
    padding: $measure--medium7 0 $measure--medium3 0;

    @include media('<=between') {
        grid-template-areas:
            'l l l l l l l l l l l l'
            'h h h h h h h h h h h h'
            'c c c c c c c c c c c c';
    }

    @include media('<=mobile') {
        grid-template-areas:
            'l l l l l l'
            'h h h h h h'
            'c c c c c c';
        padding: $measure--medium3 0;
    }
}

.banner__content__labels {
    grid-area: l;
    text-align: center;
    font-family: 'Plex Sans';
    font-weight: $font-weight-bold;
    @include smalltype();
    color: $color-gray7;
}

.banner__content__header {
    grid-area: h;
    text-align: center;
}

.banner__content__text {
    grid-area: c;
    @include standfirst();
    max-width: none;

    p {
        max-width: none;
    }

    @include media('>mobile') {
        text-align: center;
    }
}
