.back-to-top {
    a {
        float: right;
        border-bottom: none;
        font-size: $font-size--smalltype;
        transition: opacity 1s ease;

        &:active {
            outline: $border-width-default $border-style-dotted $color-nearblack;
            background: none;
            border-bottom-width: 0px;
        }

        &:focus {
            outline: $border-width-default $border-style-dotted $color-nearblack;
            border-bottom-width: 0px;
        }

        &:hover {
            color: $color-gray10;
            background: none;
            text-decoration: none;
            border-bottom-width: 0px;
        }

        &:hover .back-to-top__button__arrow,
        &:active .back-to-top__button__arrow {
            background: $color-gray10;
        }

        &:hover .back-to-top__button__label,
        &:active .back-to-top__button__label {
            color: $color-gray10;
            border-bottom: $border-width-default $border-style-solid
                $color-gray10;
        }

        & svg {
            display: none;
        }
    }
    @include media('print') {
        display: none;
    }
}

.back-to-top__button__label {
    color: $color-gray7;
    border-bottom: $border-width-default $border-style-solid $color-gray7;
}

.back-to-top-stick {
    position: fixed;
    right: 20px;
    bottom: 10px;
    z-index: 100;
    text-decoration: none;
    width: 48px;
    opacity: 0;
    overflow: hidden;

    @include media('<=mobile') {
        right: 5px;
        bottom: 5px;
    }
}

.back-to-top__button__arrow {
    color: $color-purewhite;
    background: $color-gray7;
    border-radius: 50%;
    padding: 8px;
    height: 30px;
    width: 30px;
    margin-left: 10px;
    display: inline-block;
    text-align: center;
    vertical-align: middle;

    &:before {
        vertical-align: middle;
        padding-top: 4px;
        font-size: 10px;
        border-top: 2px solid $color-purewhite;
    }
}

.back-to-top-stick .back-to-top__button {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: block;
    position: relative;

    & svg {
        display: block;
    }

    & svg path {
        fill: none;
        stroke: $color-nearblack;
        stroke-width: 5;
    }
}

.back-to-top-stick .back-to-top__button__arrow {
    padding-top: 17px;
    left: 4px;
    text-align: center;
    top: 4px;
    display: block;
    position: absolute;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    margin-left: 0px;

    &:before {
        border-top: 2px solid $color-purewhite;
        padding-top: 6px;
        vertical-align: initial;
        font-size: 13px;
    }
}

.back-to-top-stick .back-to-top__button__label {
    display: none;
}

.back-to-top-show {
    opacity: 0.75 !important;
    transition: opacity 1s ease;
}
